export default {
    data() {
        return {

        }
    },
    methods: {
        // 
        getCode() {
            var _time = parseInt(new Date().getTime() / 1000); // 1
            _time = _time + 15;
            var _newt, _endT;
            if (this.mobile) {
                var pitch = /^1[345789]\d{9}$/;
                if (!pitch.test(this.mobile)) {
                    this.$toast.top("");
                    return false;
                } else {
                    this.$http.user.getSmsCode(this.mobile)
                        .then(res => {
                            if (res.data.Success) {
                                this.$toast.top("");
                                var InterValObj = setInterval(() => {
                                    _newt = parseInt(new Date().getTime() / 1000); // 2
                                    _endT = _time - _newt;
                                    if (_endT > 0) {
                                        this.getCodeText = _endT + "s";
                                        this.disabled = true;
                                    } else {
                                        this.getCodeText = "";
                                        this.disabled = false;
                                        window.clearInterval(InterValObj);
                                    }
                                }, 1000);
                            }
                        })
                        .catch(() => {
                            this.$toast.top("");
                        });
                }
            } else {
                this.$toast.top("");
                this.disabled = false;
                return false;
            }
        }
    }
}


