// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import api from './plus/api'
import store from './store'
// 
import VueLazyload from 'vue-lazyload'
// 
import './components/Toast/toast.css'
import Toast from './components/Toast/index'
Vue.use(Toast);
// 
import VideoPlayer from "vue-video-player";
require("video.js/dist/video-js.css");
require("vue-video-player/src/custom-theme.css");
Vue.use(VideoPlayer);
// 
import Dialog from './components/dialog/index'
Vue.use(Dialog);

import CreateDialog from './components/cube-components/dialog/index.js'
Vue.use(CreateDialog);
// Vue.prototype.$createDialog = CreateDialog;

// 
import Tips from './components/tips/index'
Vue.use(Tips)
// 
import vueSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
Vue.use(vueSwiper);
// 
import VueI18n from "vue-i18n";
Vue.use(VueI18n);
// 
import './components' 
// 
import filters from './filters'
for (var key in filters) {
    Vue.filter(key, filters[key]);
}
// event-bus
import Bus from './bus'
Vue.use(Bus)

Vue.config.productionTip = false
Vue.prototype.$http = api

Vue.use(VueLazyload, {
    error: "http://cdn.meb.com/default-s",
    loading: "http://cdn.meb.com/default-s"
});

const i18n = new VueI18n({
locale: 'zh', //
messages: {
'zh': require('@/common/lang/zh.js'),
'en': require('@/common/lang/en.js')
}
})

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    i18n,
    render: h => h(App),
    data: {
      eventBus: new Vue()
    }
})

//
window.addEventListener('online', function () {
  store.dispatch('network', {
      onLine: true
  })
});
window.addEventListener('offline', function () {
  store.dispatch('network', {
      onLine: false
  })
});
//
window.addEventListener("load", function(event) {
  //console.log("");
  store.dispatch('loadchange', {
    onLoad: false
  })
});
//dom
document.addEventListener("DOMContentLoaded", function(event) {
  //console.log("DOM");
});
window.onbeforeunload = onbeforeunload_handler;
window.onunload = onunload_handler;
function onbeforeunload_handler() {
  //console.log('()');
  store.dispatch('loadchange', {
    onLoad: true
  })
}

function onunload_handler() {
  //console.log('');
  store.dispatch('loadchange', {
    onLoad: false
  })
}

