<template>
    <div class="m-swiper-box">
        <swiper :options="swiperOption" class="m-swiper-item">
            <swiper-slide
                class="m-swiper-slide-item"
                v-for="(item,index) in swiperData"
                :key="index"
            >
                <slot name="slide" />
            </swiper-slide>
            <!--  -->
            <div class="swiper-pagination"  slot="pagination" v-if="pagination"></div>
            <!--  -->
            <div class="swiper-button-prev" slot="button-prev" v-if="navigation"></div>
            <div class="swiper-button-next" slot="button-next" v-if="navigation"></div>
            <!--  -->
            <div class="swiper-scrollbar" slot="scrollbar" v-if="scrollbar"></div>
        </swiper>
    </div>
</template>

<script>
// import vueSwiper from 'vue-awesome-swiper'
// import 'swiper/dist/css/swiper.css'
export default {
    name: 'mswiper',
    props: {
        pagination: {type: Boolean, default: false}, // 
        navigation: {type: Boolean, default: false}, // 
        scrollbar:  {type: Boolean, default: false}, // 

        swiperData: {type:Array, default: [1,2,3,4,5]}, // swiper
        swiperOption: {type: Object, default: this.swiperOptions}, // swiper
    },
    data() {
        return {
            swiperOptions: {
                speed: 500, // 
                autoplay: { // 
                    delay: 3000, // 
                    disableOnInteraction: false, // 
                    stopOnLastSlide: false, // trueslideloop
                    reverseDirection: false, // 
                    waitForTransition: false, // slide
                },
                loop: false, // 
                lazy: { // 
                    loadPrevNext: true,
                },
                grabCursor: true, // Swiper
                centeredSlides: false, // 
                // slidesPerView: 'auto',
                slidesPerView: 1,
                slidesPerGroup: 1, // slides
                slidesPerColumn: 1, // 2
                slidesPerColumnFill: 'row', //column 
                effect: 'coverflow', //   fadecubeflip
                coverflowEffect: {
                    rotate: 20,
                    stretch: -10,
                    depth: 50,
                    modifier: 1,
                    slideShadows : true
                },
                // 
                pagination: {
                    el: ".swiper-pagination",
                    clickable: true
                },
                spaceBetween: 15, // slidepx
                
                // 
                scrollbar: {
                    el: '.swiper-scrollbar',
                },
                // 
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev',
                },
            },
        }
    },
    created() {

    },
    methods: {

    }
}
</script>

<style lang="less">
.m-swiper-box{
    width:100%;
    height:100%;
    position:relative;
    top:0;
    left:0;
}
.m-swiper-item{
    display: block;
    width:100%;
    height:100%;
    & > .swiper-pagination-bullets {
        bottom: 5px;
        left: 0;
        width: 100%;
        .swiper-pagination-bullet {
            width: 10px;
            height: 10px;
            box-shadow: 0px 0px 1px 2px #fff inset;
        }
        .swiper-pagination-bullet-active{
            background: #fff;
            box-shadow: 0px 0px 1px 3px rgba(0, 0, 0, 0.6) inset;
        }
    }
}
.m-swiper-slide-item{
    display: block;
    width:100%;
    height:100%;
    background-color:#ccc;
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>


