'use strict';
import http from '../http'
import options from '../http/config'

const doctor = {
    /**
     * 
     * @param {id}
     */
    getDoctorDetail(filter) {
        return http.get("/app/v6/sdoctor/GetMDoctorDetails", {
            params: filter
        });
    },

    /**
     * Hospital
     * @params {sname=spellname,page=}
     */
    getDoctorFilter(filter) {
        return http({
            baseURL: options.YG_API,
            url: "/api/app/v1.0/Project/Select",
            method: "get",
            params: filter
        });
    },
    /**
     * Hospital
     * @params {}
     */
    getAreaList() {
        return http.get("/app/v5/area/getlist");
    },
    /**
     * Hospital
     * @params {sname=spellname,page=,areaId=id,projectId=idtype=sort=}
     */
    getDoctorList(filter) {
        return http.get("/app/v6/sdoctor/GetDoctorList", {
            params: filter
        });
    }
};

export default doctor;


