'use strict';
import http from '../http'
import options from '../http/config'

const hospital = {

    /**
     * 
     * @param {id}
     */
    getHospitalDetail(filter) {
        return http.get("/app/v6/shospital/GetMHospitalDetail", {
            params: filter
        });
    },

    /**
     * Hospital
     * @params {sname=spellname,page=}
     */
    getHospitalFilter(filter) {
        return http({
            baseURL: options.YG_API,
            url: "/api/app/v1.0/Project/Select",
            method: "get",
            params: filter
        });
    },
    /**
     * Hospital
     * @params {}
     */
    getAreaList() {
        return http.get("/app/v5/area/getlist");
    },
    /**
     * Hospital
     * @params {sname=spellname,page=,areaId=id,projectId=idtype=sort=}
     */
    getHospitalList(filter) {
        return http.get("/app/v6/shospital/GetMHospitalList", {
            params: filter
        });
    }
};

export default hospital;


